.techContainer {
  display: flex;
  width: 100%;
  flex-direction: column;
  place-items: flex-start;
  padding: 10px 20px;
  background: rgb(245, 244, 244);
}
.techContainer > .title {
  margin-bottom: 20px;
  font-size: 22px;
  font-weight: 900;
}
.techItem__container {
  display: flex;
  flex-wrap: wrap;
}
.techItem {
  display: flex;
  flex-direction: column;
  place-items: center;
  margin-right: 20px;
  font-size: 14px;
  width: 50px;
}
.techItem > img {
  margin-bottom: 10px;
  width: 40px;
}

@media (max-width: 780px) {
  .techContainer {
    display: flex;
    width: 100%;
    flex-direction: column;
    place-items: flex-start;
    padding: 10px 20px;
    background: rgb(233, 233, 233);
  }
  .techContainer > .title {
    margin-bottom: 20px;
    font-size: 22px;
    font-weight: 900;
  }
  .techItem__container {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
  }
  .techItem {
    display: flex;
    flex-direction: column;
    place-items: center;
    margin-right: 20px;
    font-size: 16px;
  }
  .techItem > img {
    margin-bottom: 10px;
    width: 30px;
  }
}
