@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700;900&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
}
a {
  text-decoration: none;
  color: black;
}
button:hover {
  cursor: pointer;
}
.App {
  font-family: sans-serif;
  display: flex;
  place-content: center;
}
.innerApp__container {
  width: 820px;
  display: flex;
  flex-direction: column;
  /* border-left : 1px  solid;
  border-right: 1px solid; */

  height: 100vh;
}

.blogBox {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: center;
}

.techName {
  margin-right: 20px;
}
.projectSection {
  display: flex;
  flex-direction: column;
  place-items: center;
  width: 100%;
}
.blogSection {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
}

@media (max-width: 780px) {
  .innerApp__container {
    width: 780px;
    display: flex;
    flex-direction: column;
    place-items: center;
  }
}

.projectBox__container {
  background: #f5f3f3;

  position: relative;
  padding: 30px;
  width: 100%;
  margin: 20px 0px;
  display: flex;
  flex-direction: column;
  border-radius: 0px;
  min-height: -webkit-fit-content;
  min-height: -moz-fit-content;
  min-height: fit-content;
  /* box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, */
  /* rgba(60, 64, 67, 0.15) 0px 2px 6px 2px; */
  /* box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px; */
  /* box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 10px 0px, rgba(60, 64, 67, 0.15) 0px 1px 10px 1px; */
  /* box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px; */
}

.projectBox__container > img {
  width: 70%;
  align-self: center;
  margin: 8px 0px;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}

.projectHeading {
  display: flex;
  justify-content: space-between;
}
.projectName {
  font-size: 40px;
  color: #212121;
  font-weight: 900;
  letter-spacing: 2px;
}
.projectDescription {
  font-size: 20px;
  font-weight: 400;
  margin: 15px 0px;
}

.projectHeading > img {
  width: 70px;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
  border-radius: 50%;
}

.techContainer {
  display: flex;
  justify-content: space-between;
}
.techButton {
  display: flex;
  align-self: flex-end;
}

.linkBtn {
  color: white;
  background-color: rgb(255, 7, 7);
  border: 0;
  font-size: 18px;
  font-weight: 600;
  padding: 7px 12px;
  margin: 2px 4px;
  transition: ease-in-out 300ms;
  width: 90px;
}
.linkBtn:hover {
  transform: scale(1.1) translateY(-10%);
  color: rgb(255, 255, 255);
  background: rgb(0, 0, 0);
}

.techUsed {
  display: flex;
  margin-top: 20px;
  width: 70%;
  flex-wrap: wrap;
}

.techUsed__items {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  margin-right: 15px;
  width: 50px;
}
.techUsed__items > img {
  width: 40px;
}

@media (max-width: 780px) {
  .projectBox__container {
    padding: 30px;
    width: 100%;
  }

  .projectName {
    font-size: 40px;
  }
  .projectDescription {
    font-size: 20px;
    font-weight: 300;
    margin-top: 20px;
  }

  .projectHeading > img {
    width: 50px;
  }

  .techContainer {
    display: flex;
    justify-content: space-between;
  }
  .techButton {
    display: flex;
    align-self: flex-end;
  }

  .linkBtn {
    color: white;
    background-color: black;
    border: 0;
    font-size: 18px;
    width: -webkit-max-content;
    width: max-content;
    margin: 2px;
  }
  .techUsed__items {
    justify-content: center;
  }

  .techUsed__items > img {
    width: 40px;
  }
}

@media (max-width: 480px) {
  .projectBox__container {
    padding: 20px;
    width: 100%;
    margin: 10px 0px;
  }
  .projectBox__container > img {
    width: 100%;
  }

  .projectName {
    font-size: 30px;
  }
  .projectDescription {
    font-size: 16px;
    font-weight: 300;
    margin-top: 10px;
  }

  .projectHeading > img {
    width: 40px;
  }

  .techContainer {
    display: flex;
    justify-content: space-between;
  }
  .techButton {
    display: flex;
    align-self: flex-end;
    flex-direction: column;
  }

  .techUsed {
    flex-wrap: wrap;
    font-size: 14px;
  }

  .linkBtn {
    color: white;
    background-color: black;
    border: 0;
    font-size: 18px;
    padding: 3px 7px;
    margin: 2px;
    width: 70px;
  }

  .techUsed__items > img {
    width: 30px;
  }
}

.blogBox__container {
  width: 360px;
  height: 200px;
  background: #eeeeee;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 10px;
  margin: 10px;
  /* box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px; */
}
.blogHeading {
  display: flex;
  justify-content: space-between;
}
.blogName {
  font-size: 24px;
  font-weight: 900;
}
.blogHeading > img {
  width: 50px;
}
.blogDescription {
  text-align: justify;
  font-weight: 500;
  width: 100%;
  line-height: 1.4em;
  text-overflow: ellipsis;
  overflow: hidden;
  padding: 0px 10px;
  display: -webkit-box;
  -webkit-line-clamp: 3; /* number of lines to show */
  line-clamp: 2;
  -webkit-box-orient: vertical;
}
.blogBtn__box {
  display: flex;
  width: 100%;
}
.blogBtn {
  padding: 4px 16px;
  width: 70px;
  background: white;
  border: 0;
  font-weight: 600;
  font-size: 16px;
  transition: ease-in-out 400ms;
  border: 1px solid black;
}
.blogBtn:hover {
  transform: scale(1.2);
  background: black;
  color: white;
}

@media (max-width: 780px) {
  .blogBox__container {
    width: 300px;
  }
}

@media (max-width: 580px) {
  .blogBox__container {
    width: 90%;
  }
}

.posterContainer {
  width: 100%;
}
.header {
  display: flex;
  justify-content: space-between;
  place-items: center;
  padding: 5px 10px;
}
.headerTitle {
  font-size: 60px;
  font-weight: 900;
  position: relative;
  height: auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.headerTitle span {
  color: rgb(248, 35, 35);
}
.socialLink {
  width: 120px;
  display: flex;
  justify-content: space-around;
  font-size: 21px;
}

.profile {
  display: flex;
  place-items: center;
  justify-content: center;
  margin: 20px 0px;
}
.profilePic {
  width: 300px;
  border-radius: 50%;
  border: 1px solid black;
}
.profileName {
  width: 300px;
  margin-left: 30px;
}

@media (max-width: 780px) {
  .headerTitle {
    font-size: 40px;
  }
  .profilePic {
    width: 200px;
  }
  .profileName {
    width: 200px;
    margin-left: 15px;
  }
  .socialLink {
    font-size: 18px;
    width: 80px;
  }
}

@media (max-width: 480px) {
  .headerTitle {
    font-size: 35px;
  }
  .profile {
    flex-wrap: wrap;
  }
  .profilePic {
    width: 150px;
  }
  .profileName {
    width: 150px;
    margin-left: 15px;
  }
  .socialLink {
    font-size: 18px;
    width: 80px;
  }
}

@media (max-width: 380px) {
  .headerTitle {
    font-size: 30px;
  }
  .profile {
    flex-wrap: wrap;
  }
  .profilePic {
    width: 140px;
  }
  .profileName {
    width: 140px;
    margin-left: 12px;
  }
  .socialLink {
    font-size: 16px;
    width: 70px;
  }
}

.techContainer {
  display: flex;
  width: 100%;
  flex-direction: column;
  place-items: flex-start;
  padding: 10px 20px;
  background: rgb(245, 244, 244);
}
.techContainer > .title {
  margin-bottom: 20px;
  font-size: 22px;
  font-weight: 900;
}
.techItem__container {
  display: flex;
  flex-wrap: wrap;
}
.techItem {
  display: flex;
  flex-direction: column;
  place-items: center;
  margin-right: 20px;
  font-size: 14px;
  width: 50px;
}
.techItem > img {
  margin-bottom: 10px;
  width: 40px;
}

@media (max-width: 780px) {
  .techContainer {
    display: flex;
    width: 100%;
    flex-direction: column;
    place-items: flex-start;
    padding: 10px 20px;
    background: rgb(233, 233, 233);
  }
  .techContainer > .title {
    margin-bottom: 20px;
    font-size: 22px;
    font-weight: 900;
  }
  .techItem__container {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
  }
  .techItem {
    display: flex;
    flex-direction: column;
    place-items: center;
    margin-right: 20px;
    font-size: 16px;
  }
  .techItem > img {
    margin-bottom: 10px;
    width: 30px;
  }
}

