.posterContainer {
  width: 100%;
}
.header {
  display: flex;
  justify-content: space-between;
  place-items: center;
  padding: 5px 10px;
}
.headerTitle {
  font-size: 60px;
  font-weight: 900;
  position: relative;
  height: auto;
  width: fit-content;
}
.headerTitle span {
  color: rgb(248, 35, 35);
}
.socialLink {
  width: 120px;
  display: flex;
  justify-content: space-around;
  font-size: 21px;
}

.profile {
  display: flex;
  place-items: center;
  justify-content: center;
  margin: 20px 0px;
}
.profilePic {
  width: 300px;
  border-radius: 50%;
  border: 1px solid black;
}
.profileName {
  width: 300px;
  margin-left: 30px;
}

@media (max-width: 780px) {
  .headerTitle {
    font-size: 40px;
  }
  .profilePic {
    width: 200px;
  }
  .profileName {
    width: 200px;
    margin-left: 15px;
  }
  .socialLink {
    font-size: 18px;
    width: 80px;
  }
}

@media (max-width: 480px) {
  .headerTitle {
    font-size: 35px;
  }
  .profile {
    flex-wrap: wrap;
  }
  .profilePic {
    width: 150px;
  }
  .profileName {
    width: 150px;
    margin-left: 15px;
  }
  .socialLink {
    font-size: 18px;
    width: 80px;
  }
}

@media (max-width: 380px) {
  .headerTitle {
    font-size: 30px;
  }
  .profile {
    flex-wrap: wrap;
  }
  .profilePic {
    width: 140px;
  }
  .profileName {
    width: 140px;
    margin-left: 12px;
  }
  .socialLink {
    font-size: 16px;
    width: 70px;
  }
}
